<script>
import Master2 from "../layouts/Master2";
// import axios from "axios";
import moment from "moment";
import {  mapState, mapActions } from "vuex";

export default {
  name: "PersonalInfoPreview",
  components: {
    Master2,
  },
  data() {
    return {
      pref_cd: null,
      postal_code_status: false,
      
    };
  },
  methods: {

    ...mapActions("person",["addMember"]),

       async nextStep() {
      if (this.postal_code_status == true) {
        this.personPreviewData.address1 =
          "" +
          this.personPreviewData.postal_code +
          " " +
          this.personPreviewData.address1 +
          this.personPreviewData.building;
           console.log("trueyyyyyyyy");
      } else {
        this.personPreviewData.address1 =
          "" +
          this.personPreviewData.postal_code +
          " " +
          this.pref_cd +
          " " +
          this.personPreviewData.address1 +
          this.personPreviewData.building;
          console.log("falseeeeeeeee");
      }

      await this.addMember(this.personPreviewData)
      if(this.personPreviewData !== null) {
        this.$router.push({
            path: "/personal-information-success/",
          });
        
      } else {
        this.$router.push({
            path: "/personal-information/",
         });
      }
      if(this.errorPageShow == true) {
        this.$router.push({
            path: "/erorr-message/"
         });
         console.log("164");
      }
      window.localStorage.clear()
      window.scrollTo(0, 0);
    },


  
    customFormatter(date) {
      return moment(date).format("YYYY-MM-DD");
    },

   closeButton() {
      this.$router.push({
            path: "/"
          });
    }

  },
  computed: {
      ...mapState("person", [
        "errorsBack",
        "loading",
        "personPreviewData",
        "address1_temp",
        "errorPageShow"
        ]),
      
    },
  created() {
    this.pref_cd = JSON.parse(localStorage.getItem("pref_cd"));
    this.postal_code_status = JSON.parse(localStorage.getItem("postal_code_status"));
  },
};
</script>

