<template>
  <div class="idcard-screen">
    <Master2>
      <template v-slot:home1>
        <br />
        <div class="base-alert">
          <span>  </span>

          <button
            type="button"
            class="close"
            aria-label="Close"
            @click.prevent="closeButton"
          >
            <span aria-hidden="true" class="close_custom"></span>
          </button>
        </div>
        
        <div class="asharea five relative">
          <div class="card_photo">
            <div class="absulatePath">
             <div class="pre-view" :disabled="loading">
                <video :src="backVideo" controls ></video>
              </div>
            </div>
            <div class="center photo-capture-actions">
            
              <div class="inlineflex">
                
              <button class="btn-step" @click="cancel" :disabled="loading">
              <i aria-hidden="true" class="fa fa-times-circle-o"></i>
             </button>
                <button class="btn-step okBtn" @click="done" :disabled="loading">
                  <i v-if="!loading"  class="fa fa-check-circle-o"></i>
                  <i v-if="loading" class="fa fa-spinner fa-spin"></i>
                </button>
              </div>
          </div>
          </div>
        </div>

        <div class="bottom-message base">
          <div class="arow"><img src="@/assets/arrow.png" alt=""></div>
          <span
            ><i aria-hidden="true" class="fa fa-info-circle"></i>&nbsp;&nbsp;
            </span
          >
        </div>
      </template>
    </Master2>
  </div>
</template>

<script>
import Master2 from "../layouts/Master2";
import { mapState, mapActions } from "vuex";

export default {
  name: "RightFaceVideo",
  components: {
    Master2,
  },
  data() {
    return {
      
    };
  },
  methods: {
     ...mapActions("camera", ["saveFinalData"]),
   
    cancel(){
      this.$router.push({
            path: "/video-face-front/",
        });
    },
    closeButton() {
      this.$router.push({
            path: "/"
          });
    },
    async done(){
      
     await this.saveFinalData(
       {
        documents_type: this.documents_type,
        idcard_front:this.idcard_front,
        idcard_back:this.idcard_back,
        idcard_side:this.idcard_side,
        idcard_selfie:this.idcard_selfie,
        idcard_face:this.idcard_face,
        face_swing:this.face_swing,
        uid:this.uid
       }
     )
     if(this.finalError){
       this.$router.push({
            path: "/erorr-message/",
        });
     }else{
        this.$router.push({
            path: "/final-success-message/",
        });
     }
     
      
    }
  },
  computed: {
     ...mapState("camera", [
       "backVideo",
       "documents_type",
       "loading",
       "idcard_front",
       "idcard_back",
       "idcard_side",
       "idcard_selfie",
       "idcard_face",
       "face_swing",
       "finalError",
       
       ]),
     ...mapState("person", ["uid"]),
    hasVideo() {
      if (this.video_url) {
        return this.video_url
      }
      return false;
    },
  }
};
</script>

