export const videoAddServer = async ({ commit }, {first_video_file, second_video_file, third_video_file}) => {
    try {

        commit("LOADING",true);
       
        let data = new FormData();
        
        data.append("first_video_file", first_video_file);
        data.append("second_video_file", second_video_file);
        data.append("third_video_file", third_video_file);
        let config = {
            header: {
              "Content-Type": "multipart/form-data",
            },
          }
        const response  = await Api.post("/upload-video", data, config)
        console.log('65',response.data);
        if(response.status == 200) {
            console.log('66',response.data);
            commit("SET_VIDEO_RESPONSE",response.data.video_url);
            commit("SET_VIDEO_RESPONSE_PATH",response.data.video_path);
           
            commit("LOADING",false);
        }
     
    } catch (error) {
        console.log('video action',error)
        commit("LOADING",false);
        commit("VIDEO_DATA_FAIL",true);
      
    }

    commit("LOADING",false);
}
