export const saveFinalData = async ({ commit }, {documents_type, idcard_front,idcard_back,idcard_side,idcard_selfie,idcard_face,face_swing,uid}) => {
    try {

        commit("LOADING",true);
       
        const response  = await Api.post("/save-file-info", {documents_type, idcard_front,idcard_back,idcard_side,idcard_selfie,idcard_face,face_swing,uid})
        console.log('65',response.data);
        if(response.status == 200) {
            console.log('92',response.data);
            commit("SET_SAVE_FINAL_DATA",response.data.message);
            
           
            commit("LOADING",false);
        }
     
    } catch (error) {
        console.log('save final data',error)
        commit("FINAL_ERROR",true);
        commit("LOADING",false);
      
    }

    commit("LOADING",false);
}