                />
                <div class="invalid-feedback" v-if="errorsBack.name">
                  <!-- {{ errors.name[0] }} -->
                  
                </div>
              </div>
            </div>
            <div class="col-md-12">
              <div class="form-group">
                <label class="control-label"></label>
                <input
                  v-model="form_data.name_kana"
                  maxlength="100"
