                  required="required"
                  class="form-control"
                  placeholder="  "
                  :class="{ 'is-invalid': errorsBack.name_kana }"
                />
                <div class="invalid-feedback" v-if="errorsBack.name_kana">
                  <!-- {{ errors.name_kana[0] }} -->
                  
                </div>
              </div>
            </div>

            <div class="col-md-6 col-sm-6 col-6 pdr7">
              <div class="form-group">
                <label class="control-label"></label>
                <input
                  v-model="form_data.postal_code"
                  maxlength="100"
                  type="text"
                  required="required"
                  class="form-control"
                  placeholder="1234567"
                  :class="{ 'is-invalid': errorsBack.postal_code }"
                  @change="getPrefecture()"
                  autocomplete="off"
                />
                <div class="invalid-feedback" v-if="errorsBack.postal_code">
                  <!-- {{ errors.postal_code[0] }} -->
                  
                </div>
              </div>
            </div>
            <!--end-->

            <div class="col-md-6 col-sm-6 col-6 pdl7"></div>
            <!--end-->

            <div class="col-md-6 col-sm-6 col-6 pdr7">
              <div class="form-group">
                <label class="control-label"></label>
                <select
                  class="form-control"
                  :class="{ 'is-invalid': errorsBack.pref_cd }"
                  v-model="form_data.pref_cd"
                >
                  <!-- <option v-bind:value="form_data.pref_cd" v-if="prefecture">
                  {{ prefecture }}
                </option>
                <option v-else ></option> -->
                  <!-- <option v-if="!prefecture" v-bind:value="form_data.pref_cd" selected>{{form_data.pref_cd}}</option>
    <option v-bind:value="form_data.pref_cd" v-else>
    {{ prefecture }}
    </option> -->
                  <option
                    v-bind:value="p.prefecture"
                    v-for="p in prefectureArray"
                    :key="p.prefecture"
                  >
                    {{ p.prefecture }}
                  </option>
                </select>
                <div class="invalid-feedback" v-if="errorsBack.postal_code">
