<script>
import Master2 from "../layouts/Master2";
import Datepicker from "vuejs-datepicker";
import axios from "axios";
import moment from "moment";
import {  mapState, mapActions } from "vuex";

export default {
  name: "PersonalInfo",
  components: {
    Master2,
    Datepicker,
  },
  data() {
    return {
      form_data: {
        name: null,
        name_kana: null,
        birthday: null,
        gender: "0",
        postal_code: null,
        pref_cd: "",
        address1: null,
        address2: null,
        building: null,
      },
      open_date: new Date(2000, 0, 1),
      prefectureArray: null,
      address1_temp: null,
      prefecture: null,
      postal_code_status: false,
      
    };
  },
  methods: {
    
    ...mapActions("person",["personAdd"]),
    async nextStep() {
     await this.personAdd(this.form_data)
      if(this.personPreviewData !== null) {
         localStorage.setItem("pref_cd", JSON.stringify(this.form_data.pref_cd))
        this.$router.push({
            path: "/personal-information-preview/",
          });
     
      this.form_data.name = null
      this.form_data.name_kana = null
      this.form_data.birthday = null
      this.form_data.postal_code = null
      this.form_data.address1 = null
      this.form_data.address2 = null
      this.form_data.building = null
      this.form_data.gender = null
      }else{
        this.$router.push({
            path: "/personal-information/",
          });
      }
    
    },
    customFormatter(date) {
      return moment(date).format("YYYY-MM-DD");
    },

    // get prefectureArray data
    getPrefecturePopulateData() {
      axios.get("https://107.155.108.129:82/api/prefectures").then((response) => {
        console.log("1101", response.data.prefectures);
        if (response.data) {
          this.prefectureArray = response.data.prefectures;
        }
      });
    },

    getPrefecture() {
      axios
        .get(
          "https://107.155.108.129:82/api/postcode?code=" +
            this.form_data.postal_code
        )
        .then((response) => {
          // console.log("prefecture code", response.data.prefecture);
          if (response.data.prefecture) {
            this.prefecture = response.data.prefecture;
            this.form_data.pref_cd = response.data.prefecture;
            this.form_data.address1 = response.data.data;
            this.postal_code_status = true;
            localStorage.setItem(
              "postal_code_status",
              JSON.stringify(this.postal_code_status)
            );
            console.log("329", this.postal_code_status);
          } else {
            this.prefecture = null;
            this.form_data.pref_cd = null;
            this.form_data.address1 = null;
            this.postal_code_status = false;
            localStorage.setItem(
              "postal_code_status",
              JSON.stringify(this.postal_code_status)
            );
            console.log("336", this.postal_code_status);
          }
        })
        .catch((error) => {
          console.log("postalcode error", error);
          this.postal_code_status = false;
          localStorage.setItem(
            "postal_code_status",
            JSON.stringify(this.postal_code_status)
          );
          console.log("336", this.postal_code_status);
        });
    },
    
  closeButton() {
      this.$router.push({
            path: "/"
          });
    }
  },
  computed: {
    
    ...mapState("person", ["errorsBack","loading","personPreviewData"])
  
  },
  created() {

    this.form_data.name = null;
    this.form_data.name_kana = null;
    this.form_data.birthday = null;
    this.form_data.gender = null;
    this.form_data.postal_code = null;
    this.form_data.pref_cd = null;
    this.form_data.address1 = null;
    this.form_data.address2 = null;
    this.form_data.building = null;
    this.getPrefecturePopulateData();
  },
};
</script>

