<template>
  <div class="idcard-screen">
    <Master2>
      <template v-slot:home1>
        <br />
        <div class="base-alert">
          <span>  </span>

          <button
            type="button"
            class="close"
            aria-label="Close"
            @click.prevent="closeButton"
          >
            <span aria-hidden="true" class="close_custom"></span>
          </button>
        </div>
        
        <div class="asharea five relative">
          <div class="card_photo">
            <div class="absulatePath">
             <div class="pre-view " v-if="hasVideo">
                <video :src="video_url" controls ></video>
              </div>
            </div>
            <div class="center photo-capture-actions">
            <label class="btn-bs-file"  v-if="!hasVideo" >
              <i aria-hidden="true" class="fa fa-video-camera"></i>
              <input type="file"  @change="takeVideo" id="recorder" accept="video/*"  capture/>
              
            </label>
              <div class="inlineflex" v-if="hasVideo">
                <!-- <button class="btn-step cancelBtn" @click="cancel">
                  <i class="fa fa-times-circle-o"></i>
                </button> -->
                <label class="btn-step  cancelBtn"  >
              <i aria-hidden="true" class="fa fa-times-circle-o"></i>
              <input type="file" @change="takeVideo" hidden :disabled="loading"/>
             </label>
                <button class="btn-step okBtn" @click="done" :disabled="loading">
                  <i v-if="!loading" class="fa fa-check-circle-o"></i>
                   <i v-if="loading" class="fa fa-spinner fa-spin"></i>
                </button>
              </div>
          </div>
          </div>
        </div>

        <div class="bottom-message base">
          <div class="arow"><img src="@/assets/arrow.png" alt=""></div>
          <span
            ><i aria-hidden="true" class="fa fa-info-circle"></i>&nbsp;&nbsp;
            </span
          >
        </div>
      </template>
    </Master2>
  </div>
</template>

<script>
import Master2 from "../layouts/Master2";
import { mapState, mapActions } from "vuex";

export default {
  name: "RightFaceVideo",
  components: {
    Master2,
  },
  data() {
    return {
      video_data: null,
      video_url: null,
    };
  },
  methods: {
     ...mapActions("camera", ["addRightFaceVideo","videoAddServer"]),
    takeVideo: async function (e) {
        let file = e.target.files[0];
        
        this.video_url = URL.createObjectURL(file);
        
        let blob = await fetch(URL.createObjectURL(file)).then(r => r.blob());
        this.video_data = blob
        await this.addRightFaceVideo(this.video_data)
    },
    cancel(){
      // document.getElementById('recorder').click();
      this.takeVideo()
      console.log('82');
    },
    async done(){
      
     await this.videoAddServer(
      {
        first_video_file: this.first_video_file,
         second_video_file:this.second_video_file,
        third_video_file:this.third_video_file
       }
     )
      if(this.video_merge_fail){
        this.$router.push({
            path: "/document-type",
        });
      }else {
        this.$router.push({
            path: "/preview-data-show",
        });
      }
      
    },
  
    closeButton() {
      this.$router.push({
            path: "/"
          });
    }
    
  },
  computed: {
     ...mapState("camera", [
       "first_video_file",
       "second_video_file",
       "third_video_file",
       "video_merge_fail",
       "loading"
       ]),
    hasVideo() {
      if (this.video_url) {
        return this.video_url
      }
      return false;
    },
  }
};
</script>

