<template>
  <div class="idcard-screen">
    <Master2>
      <template v-slot:home1>
        <br />
        <div class="base-alert">
          <span>  </span>

          <button
            type="button"
            class="close"
            aria-label="Close"
            @click.prevent="closeButton"
          >
            <span aria-hidden="true" class="close_custom"></span>
          </button>
        </div>

        <div class="asharea five relative">
          <div class="card_photo">
            <div class="absulatePath" v-if="hasImage">
              <div class="moldarea">
                <div  class="text-center">
                  <img   src="@/assets/mold2.png" alt="" class="mold-area"></div>
              </div>
            </div>
            <croppa
              v-model="myCroppa"
              canvas-color="transparent"
              auto-sizing
              :height="resizableH"
              initial-position="center"
              :show-remove-button="false"
              disable-click-to-choose
            ></croppa>
            <div class="center photo-capture-actions">
              <button
                class="btn flex-center capturebtn"
                @click="takingSnap"
                v-if="!hasImage"
              >
                <i class="fa fa-camera"></i>
              </button>
              <div v-if="hasImage">
                <button class="btn-step cancelBtn" @click="remove">
                  <i class="fa fa-times-circle-o"></i>
                </button>
                <button class="btn-step okBtn" @click="generateImage">
                  <i class="fa fa-check-circle-o"></i>
                </button>
              </div>
            </div>
          </div>
        </div>

        <div class="bottom-message base">
          <div class="arow"><img src="@/assets/arrow.png" alt=""></div>
          <span
            ><i aria-hidden="true" class="fa fa-info-circle"></i>&nbsp;&nbsp;
            </span
          >
        </div>
      </template>
    </Master2>
  </div>
</template>

<script>
import Master2 from "../layouts/Master2";
import Vue from "vue";
import Croppa from "vue-croppa";
Vue.use(Croppa);
import { mapActions } from "vuex";

export default {
  name: "MoldFace",
  components: {
    Master2,
  },
  data() {
    return {
      myCroppa: null,
      imgUrl: "",
      resizing: false,
      resizableW: 550,
      resizableH: 550,
    };
  },
  methods: {
    ...mapActions("camera", ["addMoldFace"]),

    takingSnap: function () {
      this.myCroppa.chooseFile();
    },

    generateImage: async function () {
      let url = this.myCroppa.generateDataUrl();
      if (!url) {
        alert("no image");
        return;
      }
      this.imgUrl = url;
      await this.addMoldFace(this.imgUrl);

      this.$router.push({
            path: "/idcard-mold-face/",
          });
    },

    remove: function () {
      this.myCroppa.remove();
      this.myCroppa.chooseFile();
    },
  
    closeButton() {
      this.$router.push({
            path: "/"
          });
    }
    
  },
  computed: {
    hasImage() {
      if (this.myCroppa) {
        return this.myCroppa.hasImage();
      }
      return false;
    },
  },
  mounted() {
    document
      .querySelector(
        "#app > div > div > div > div.asharea.five.relative > div > div.croppa-container > input[type=file]"
      )
      .setAttribute("capture", "user");
  },
};
</script>

<style scoped>
.croppa-container {
  text-align: center;
}
</style>