/*System Importing*/
import Vue from 'vue'
import VueRouter from 'vue-router'

/*Custom Components Importing*/
import Home from '../components/pages/Home'

import PersonalInfo from "../components/pages/PersonalInfo";
import PersonalInfoPreview from "../components/pages/PersonalInfoPreview";
import PersonalSuccessMessage from "../components/pages/PersonalSuccessMessage";
import DocumentType from "../components/pages/DocumentType";
import IdcardFront from "../components/pages/IdcardFront";
import IdcardBack from "../components/pages/IdcardBack";
import IdcardSide from "../components/pages/IdcardSide";
import MoldFace from "../components/pages/MoldFace";
import IdcardMoldFace from "../components/pages/IdcardMoldFace";
import VideoFaceFront from "../components/pages/VideoFaceFront";
import LeftFaceVideo from "../components/pages/LeftFaceVideo";
import RightFaceVideo from "../components/pages/RightFaceVideo";
import PreviewVideo from "../components/pages/PreviewVideo";
import FinalSuccessMsg from "../components/pages/FinalSuccessMsg";
import ErrorMessage from "../components/pages/ErrorMessage";




/*Inserting VueRouter to the Vue Framework*/
Vue.use(VueRouter);


const routes = [

    {path: '/', component: Home, name: 'Home'},
  
    {path: '/personal-information/', component: PersonalInfo, name: 'PersonalInfo'},
    {path: '/personal-information-preview/', component: PersonalInfoPreview, name: 'PersonalInfoPreview'},
    {path: '/personal-information-success/', component: PersonalSuccessMessage, name: 'PersonalSuccessMessage'},
    {path: '/document-type', component: DocumentType, name: 'DocumentType'},
    {path: '/idcard-front', component: IdcardFront, name: 'IdcardFront'},
    {path: '/idcard-back', component: IdcardBack, name: 'IdcardBack'},
    {path: '/idcard-side', component: IdcardSide, name: 'IdcardSide'},
    {path: '/mold-face', component: MoldFace, name: 'MoldFace'},
    {path: '/idcard-mold-face', component: IdcardMoldFace, name: 'IdcardMoldFace'},
    {path: '/video-face-front', component: VideoFaceFront, name: 'VideoFaceFront'},
    {path: '/video-left-face', component: LeftFaceVideo, name: 'LeftFaceVideo'},
    {path: '/video-right-face', component: RightFaceVideo, name: 'RightFaceVideo'},
    {path: '/preview-data-show', component: PreviewVideo, name: 'PreviewVideo'},
    {path: '/final-success-message', component: FinalSuccessMsg, name: 'FinalSuccessMsg'},
    {path: '/erorr-message', component: ErrorMessage, name: 'ErrorMessage'},

];


export const router = new VueRouter({
    mode: 'history', //hides hash in url after localhost:8080
    routes: routes
});

